.profileContainer {
  display: flex;
  height: 100vh;
  width: 100vw;
  max-width: 100%;
  background-color: white;
  /* margin-top: 2rem; */
}


body::-webkit-scrollbar {
  display: none;
}


.profileContainer>div {
  display: flex;
  height: 80vh;
  width: 100vw;
  max-width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 0px;
}

.profileHeading {
  color: rgba(255, 99, 71, 0.751);
  font: 900 3.2vmax "Roboto";
  position: relative;
  z-index: 9;
  text-align: center;
  margin-top: 2vmax;
  width: 100%;
}

.profileContainer>div:first-child>img {
  width: 20vmax;
  height: 20vmax;
  object-fit: fill;
  border-radius: 80%;
  transition: all 0.5s;
}

.profileContainer>div:first-child>img:hover {
  transform: scale(1.05);
}

.profileContainer>div:first-child>a {
  border: none;
  background-color: tomato;
  font: 400 1.2vmax "Roboto";
  color: white;
  text-decoration: none;
  padding: 0.5vmax;
  width: 30%;
  margin: 4vmax;
  text-align: center;
  transition: all 235ms ease-in-out;
}

.profileContainer>div:first-child>a:hover {
  background-color: rgb(204, 78, 56);
}

.profileContainer>div:last-child {
  justify-content: space-evenly;
  align-items: flex-start;
  padding: 5vmax;
  box-sizing: border-box;
}

.profileContainer>div:last-child>div>h4 {
  color: rgba(0, 0, 0, 0.418);
  font: 400 1.2vmax "Roboto";
}

.profileContainer>div:last-child>div>p {
  color: rgb(0, 0, 0);
  font: 400 1.2vmax cursive;
  margin: 0.2vmax;
}

.profileContainer>div:last-child>div:last-child {
  display: flex;
  flex-direction: column;
  width: 60%;
}

.profileContainer>div:last-child>div:last-child>a {
  border: none;
  background-color: rgb(68, 68, 68);
  font: 400 1.2vmax "Roboto";
  color: white;
  text-decoration: none;
  padding: 0.5vmax;
  text-align: center;
  transition: all 235ms ease-in-out;
  margin: 1vmax 0;
}

.profileContainer>div:last-child>div:last-child>a:hover {
  color: rgb(68, 68, 68);
  font: 400 1.3vmax "Roboto";
  background-color: white;
  transition: all 235ms ease-in-out;
  transform: scale(1.004);
  border: 1px solid rgb(68, 68, 68);
}

@media screen and (max-width: 600px) {
  .profileContainer {
    flex-direction: column;
  }

  .profileContainer>div:first-child>h1 {
    font: 500 3.2vmax "Roboto";
    transform: translateY(-2vmax);
  }

  .profileContainer>div:first-child>a {
    font: 400 1.7vmax "Roboto";
    padding: 1vmax;
  }

  .profileContainer>div:last-child {
    text-align: center;
    align-items: center;
  }

  .profileContainer>div:last-child>div>h4 {
    font: 400 2.5vmax "Roboto";
  }

  .profileContainer>div:last-child>div>p {
    font: 400 2vmax cursive;
    margin: 0.5vmax 0;
  }

  .profileContainer>div:last-child>div:last-child>a {
    font: 400 1.8vmax "Roboto";
    padding: 1vmax;
    margin: 2vmax 0;
  }
}